import Button from 'react-bootstrap/Button';
import { CardGroup } from 'react-bootstrap';
import { Card } from 'react-bootstrap';
import dd from './Diamond.jpg';
import Swal from 'sweetalert2';
import { Container } from 'react-bootstrap';
import { Row } from 'react-bootstrap';

function aa() {
  let timerInterval
Swal.fire({
  title: '!!',
  html: ' ()',
  timer: 3000,
  timerProgressBar: true,
  didOpen: () => {
    Swal.showLoading()
    const b = Swal.getHtmlContainer().querySelector('b')
  },
  willClose: () => {
    clearInterval(timerInterval)
  }
}).then((result) => {
  /* Read more about handling dismissals below */
  if (result.dismiss === Swal.DismissReason.timer) {
    console.log(' xD')
  }
})
}



function usage() {
  const swalWithBootstrapButtons = Swal.mixin({
    customClass: {
      confirmButton: 'btn btn-success',
      cancelButton: 'btn btn-danger'
    },
    buttonsStyling: false
  })
  
  swalWithBootstrapButtons.fire({
    title: '?',
    text: "!()",
    icon: 'warning',
    showCancelButton: false,
    confirmButtonText: '! ',
    reverseButtons: true,
    footer: '<a href="/">?</a>'
  }).then((result) => {
    if (result.isConfirmed) {
      swalWithBootstrapButtons.fire(
        '!',
        '',
        'error'
      )
    } 
  })
}

function fake() {
    Swal.fire({
      title: 'You Cannot Remove this!',
      icon:"error",
      html: '',
      timer: 1000,
      timerProgressBar: true,
      didOpen: () => {
        Swal.showLoading()
        const b = Swal.getHtmlContainer().querySelector('b')
      },
    })
}


const ACard = ()=> {
  return (
    <Container>
      <Row>
      <div className="col-12 col-md-3">
            <Card>
            <Card.Body>
              <Card.Title>
                <div className="text1"> 2.0</div>
              </Card.Title>
              <Card.Text as='div'> 
                <div className="text2">2.0</div>
                <div className="text2">!</div>
                <div className="text2">React.js ...</div>
                <div className="text2">By. YuDong</div>
              </Card.Text>
              <Button variant="danger" onClick={aa}></Button>
            </Card.Body>
          </Card>
        </div>
        <div className="col-12 col-md-3">
          <Card>
            <Card.Body>
              <Card.Title>
                <div className="text1"></div>
              </Card.Title>
              <Card.Text as='div'> 
                <div className="text2">  </div>
                <div className='text2'> !</div>
                <div className="text2">2.0,!</div>
                <div className="text2">!</div>
              </Card.Text>
              <Button variant="danger" onClick={usage}></Button>
            </Card.Body>
          </Card>
        </div>
        <div className="col-12 col-md-3">
          <Card >
            <Card.Body>
              <Card.Title>
                <div className="text1">:</div>
              </Card.Title>
              <Card.Text as='div'> 
                <div className="text2">2.0...</div>
                <div className="text2"></div>
                <div className="text2">!</div>
                <div className="text2">!</div>
              </Card.Text>
              <Button variant="danger" onClick={fake}></Button>
            </Card.Body>
          </Card>
        </div>
        <div className="col-12 col-md-3">
          <Card className='h-100'>
            <Card.Body>
              <Card.Title>
                <div className="text1"></div>
              </Card.Title>
              <Card.Text as='div'> 
                <div className="text2"></div>
                <div className="text2"></div>
                <div className="text2"> </div>
                <div className="text2"> </div>
              </Card.Text>
              <Button variant="danger" onClick={fake}></Button>
            </Card.Body>
          </Card>
        </div>
      </Row>
    </Container>
  );
}

export default ACard;